
/* 
   Attribute 
*/
export class Attribute {
 
    // Attribute name 
    public name: string;
 
    // Attribute value type: string, int, bool 
    public type: string;
 
    // The string value in case of attribute type: string 
    public valueString: string;
 
    // The int value in case of attribute type: int 
    public valueInt: number;
 
    // The boolean value in case of attribute type: bool 
    public valueBool: boolean;
 
    constructor(name?: string, type?: string, valueString?: string, valueInt?: number, valueBool?: boolean) { 
        this.name = name;
        this.type = type;
        this.valueString = valueString;
        this.valueInt = valueInt;
        this.valueBool = valueBool;
    }

}


